import * as React from 'react'
import Box from '@mui/material/Box'
import Modal from '@mui/material/Modal'
import { Summary } from '../Summary/Summary'
import { useAppSelector } from '../../app/hooks'
import {
  getLegosDetails,
  getLegosPartsDetails
} from '../../features/minifigs/legoSlice'

interface ModalProps {
  setModal: (modal: boolean) => void
  modal: boolean
}

const style = {
  position: 'absolute' as 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 400,
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4
}

export const DetailsModal: React.FC<ModalProps> = ({ modal, setModal }) => {
  const handleClose: (modal: boolean) => void = () => setModal(false)
  const legoPartsDetails: any = useAppSelector(getLegosPartsDetails)
  const legoDetails: any = useAppSelector(getLegosDetails)

  return (
    <div>
      <Modal
        open={modal}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
          <Summary
            legoDetails={legoDetails}
            legoParts={legoPartsDetails}
            maxHeight={false}
          />
        </Box>
      </Modal>
    </div>
  )
}
